            <span>拍一拍</span>
          </div>
          <div class="item" @click="toggleHome">
            <Icon name="转换-02"/>
            <span>换一换</span>
          </div>
        </div>
      </div>
      <div class="homeBg">
        <img src="@/assets/images/home/homeBg2.jpg" alt="homeRiver">
      </div>
      <div v-for="(scene,index) in sceneImgList" :key="scene[index]"
           @click="showSceneInfo(index);select(index)"
           :class="['scene'+index,{selected:selected===index}] "
           :style="{'background-image':`url(${selected===index?scene.img2:scene.img})`}">
        <span>{{ scene.name }}</span>
      </div>
      <div v-show="isShowSceneInfo" class="detail-wrapper">
        <div class="scene-info">
          <van-swipe class="scene-info-swiper"
                     ref="vanSwipe"
                     @change="onChange">
            <van-swipe-item
                v-for="item in sceneIntro[0]" :key="item.name">
              <router-link :to="`/category/${index + '-' + currentSceneInfoIndex}`">
                <img :src="item.img" :alt="item.name">
              </router-link>
              <span>{{ item.name }}</span>
            </van-swipe-item>
          </van-swipe>
          <div class="btn">
            <router-link custom v-slot="{navigate}"
                         :to="`/category/${index + '-' + currentSceneInfoIndex}`">
              <button @click="navigate">了解更多</button>
            </router-link>
            <button @click="nextScene">下一场景</button>
          </div>
          <div class="close" @click="closeSceneInfo">
            <Icon name="close"/>
          </div>
        </div>
        <div class="close-btn">
          <Icon name="add" @click="closeSceneInfo"/>
        </div>
      </div>
