           @click="showSceneInfo(index)"
           :class="['scene','scene'+index]"
           :style="{'background-image':`url(${scene.img})`}">
        <span>{{ scene.name }}</span>
      </div>
      <div v-show="isShowSceneInfo" class="scene-info-wrapper">
        <div class="scene-info">
          <van-swipe class="scene-info-swiper"
                     ref="vanSwipe"
                     @change="onChange">
            <van-swipe-item
                v-for="item in sceneIntro[0]" :key="item.name">
              <router-link :to="`/category/${index + '-' + currentSceneInfoIndex}`">
                <img :src="item.img" :alt="item.name">
              </router-link>
              <span>{{ item.name }}</span>
            </van-swipe-item>
          </van-swipe>
          <div class="btn">
            <router-link custom v-slot="{navigate}"
                         :to="`/category/${index + '-' + currentSceneInfoIndex}`">
              <button @click="navigate">了解更多</button>
            </router-link>
            <button @click="nextScene">下一场景</button>
          </div>
          <div class="close" @click="closeSceneInfo">
            <Icon name="close"/>
          </div>
        </div>
        <div class="close-btn">
          <Icon name="add" @click="closeSceneInfo"/>
        </div>
      </div>
