<script>
import {
  ss1, ss2, ss3, ss4, ss5, ss6, ss7,
} from '@/assets/images/home/scene'

import {
  scene1, scene2,
  scene3, scene4,
  scene5, scene6,
  scene7
} from '@/assets/images/home/七幕场景切图'


export default {
  name: 'HomeView',
  inject: ['reload'],
  data() {
    return {
      index: 0,
      currentSceneInfoIndex: 0,
      cover: false,
      hasNav: false,
      isShowSceneInfo: false,
      isShowMenu: false,
      sceneImgList: [
        {
          name: '盘古大陆',
          img: ss1,
        },
        {
          name: '古希腊-海伦',
          img: ss2
        },
        {
          name: '古埃及-克利奥帕特拉',
          img: ss3
        },
        {
          name: '中世纪中国-杨贵妃',
          img: ss4
        },
        {
          name: '现代日本-吉野太夫',
          img: ss5
        },
        {
          name: '现代日本-武藏',
          img: ss6
        },
        {
          name: '阿玛西亚',
          img: ss7
        },
        {
          name: '',
        }
      ],
      sceneIntro: {
        0: [{
          name: '第一场 泛大陆',
          img: scene1.img1,
        }],
        1: [
          {name: '第二场 海伦，美丽女神', img: scene2.img2,},
          {name: '第三场 特洛伊战争', img: scene2.img3,}
        ],
        2: [
          {name: '第四场 克利奥帕特拉与凯撒相遇', img: scene3.img4,},
          {name: '第五场 阿克提姆战役', img: scene3.img5,}
        ],
        3: [
          {name: '第六场 杨贵妃', img: scene4.img6,},
          {name: '第七场 安禄山之乱', img: scene4.img7,}
        ],
        4: [
          {name: '第八场 从长安到京都', img: scene5.img8,},
          {name: '第九场 吉野太夫', img: scene5.img9,}
        ],
        5: [
          {name: '第十场 武藏之战', img: scene6.img10,},
          {name: '第十一场 武藏：与虚荣作斗争', img: scene6.img11,},
          {name: '第十二场 没有虚荣的世界', img: scene6.img12,}
        ],
        6: [
          {name: '第十三场 四个标志', img: scene7.img13,},
          {name: '第十四场 阿玛西亚超大陆', img: scene7.img14,},
        ]
      }
    }
  },
  methods: {
    showSceneInfo(index) {
      const firstScene = [{name: '第一场 泛大陆', img: scene1.img1,}]
      this.sceneIntro[0] =
          index === 0 ? firstScene : this.sceneIntro[index]
      this.isShowSceneInfo = true
      this.index = index
    },
    closeSceneInfo() {
      this.isShowSceneInfo = false
    },
    onChange(index) {
      this.currentSceneInfoIndex = index
    },
    nextScene() {
      this.$refs.vanSwipe.count > 1 && this.$refs.vanSwipe.next()
    },
    openMenu() {
      this.isShowMenu = !this.isShowMenu
    },
    changeHomeTopBar() {
      const homeTopBar = this.$refs.homeTopBar
      const home = document.querySelector('.home')
      home.addEventListener('touchmove', () => {
        if (home.scrollTop > 20) {
          homeTopBar.classList.add('active')
        } else if (homeTopBar.scrollTop < 40) {
          homeTopBar.classList.contains('active')
          && homeTopBar.classList.remove('active')
        }
      })
    },
    scan() {
      this.$router.push('/cameraTest')
    },
    toggleHome() {
      const a = window.localStorage.getItem('nftHome')
      a ? window.localStorage.setItem('nftHome', `${a === '0' ? '1' : '0'}`)
          : window.localStorage.setItem('nftHome', '1')
      this.reload()
    },

  },
  beforeMount() {
    const data = sessionStorage.getItem('show1')
    if (data === null) {
      sessionStorage.setItem('show1', 'true')
      setTimeout(() => {
        this.cover = true
      }, 3000)
    } else {
      this.cover = true
    }
  },
  mounted() {
    this.changeHomeTopBar()
  }
}
</script>

