<template>
  <nav class="nav">
    <router-link to="/" class="item">
      <Icon :name="home"/>
      <span>首页</span>
    </router-link>
    <router-link to="/movies" class="item">
      <Icon :name="movies"/>
      <span>看电影</span>
    </router-link>
    <router-link to="/exhibition" class="item">
      <Icon :name="exhibition"/>
      <span>观展览</span>
    </router-link>
    <router-link to="/my" class="item">
      <Icon :name="my"/>
      <span>我的</span>
    </router-link>
  </nav>
</template>

<script>
export default {
  name: 'Nav',
  data() {
    return {
      home: 'home1',
      movies: 'movies1',
      exhibition: 'exhibition1',
      my: 'my1'
    }
  },
  methods: {
    setIcon(name) {
      this[name] = name + 2
    }
  },
  watch: {
    $route: {
      immediate: true,
      handler(to, from) {
        this.setIcon(to.name === 'index' ? 'home' : to.name)
      },
    },
  }

}
</script>

